.header{
    display:flex;
    justify-content: space-between;
    flex:1;
    padding:12px;
    position:relative;
    align-items:center;
}

.menu{
    display:flex;
    flex:1;
    justify-content: flex-end;

}

.menu-icon{
    font-size:24px;
}

.mobile-menu{
    display:none;
}

.web-menu{
    display:flex;
}

.logo{
    display:flex;
    font-size:50px;
    color: #232f3e;
    font-weight: 800;
}

.logo:hover{
    color: #232f3e;
    transition: all .5s;
    transform : translateY(10px);
}

@media only screen and (max-width:720px) {
    .mobile-menu{
        display:flex;
    }

    .web-menu{
        display:none;
    }

    .logo{
        font-size:24px;
    }
}

@media only screen and (max-width:512px) {
    .home{
        width:100%;
    }
}